.go-back, .form-container {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.go-back a {
  color: #333;
  margin-bottom: 20px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.form {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.form label {
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.form input[type="number"] {
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-top: 5px;
  padding: 10px;
  font-size: 16px;
}

.form button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 18px;
  transition: background-color .3s;
}

.form button:hover {
  background-color: #45a049;
}

.form button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

